@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Oswald:wght@300;400;700&display=swap);
/* font-family: 'Amatic SC', cursive;
font-family: 'Oswald', sans-serif;*/

:root {
  --main-color: #10bb4f;
  --light-color: #5eef7e;
  --dark-color: #008921;
  /*var(--main-color)*/
}

#root {
  display: grid;
  height: 100vh;
  grid-template: auto 1fr auto / auto 1fr auto
}

.amatic-letter {
  font-family: 'Amatic SC', cursive;
}

body {
  font-family: 'Oswald', sans-serif;
}

img {
  width: 100%;
}

.center-page {
  display: flex;
  justify-content: center;
  align-items: center;
}

/*************NAVBAR****************/

.logo-img {
  width: 80px;
  margin-top: -20px;
}

.logo-letter {
  color: #10bb4f;
  color: var(--main-color);
  font-weight: bold;
  font-size: 2em;
  align-self: baseline;
  text-transform: uppercase;
  text-decoration: none;
}

.logo-letter a:hover {
  text-decoration: none;
  color: #10bb4f;
  color: var(--main-color);
}

.navbar {
  border-bottom: solid 3px #008921;
  border-bottom: solid 3px var(--dark-color);
  background-color: white;
  box-sizing: unset;
  box-shadow: 0px 5px 5px 0px rgba(161, 161, 161, 1);
  padding-right: 15px;
  background: lightpink;
  grid-column: 1 / 4;
}

.margin-right {
  margin-right: 25px;
}

/*************PROFILE****************/

.left-sidebar {
  background: lightblue;
  grid-column: 1 / 2;
  padding: 1rem;
}

main {
  background: coral;
  grid-column: 2 / 3;
  padding: 20px;
}

footer {
  background: wheat;
  padding: 2rem;
  text-align: center;
  grid-column: 1 / 4;
}


.spinner {
    width: 150px;
    margin: 0 auto;
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
    -webkit-animation-name: pulse;
            animation-name: pulse;
}



@-webkit-keyframes pulse {
    0% {
        transform: scale(.8);
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(.8);
    }
}



@keyframes pulse {
    0% {
        transform: scale(.8);
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(.8);
    }
}
.login-page {
    display: flex;
    justify-content: center;
    align-items: center;
}

.lft-box {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.lft-box-text {
    font-weight: bold;
    font-size: 2em;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    text-transform: uppercase;
}

.flex-end {
    align-items: flex-end;
}

.form-login {
    border: solid 3px var(--dark-color);
    border-radius: 3%;
    padding: 90px 50px;
}

.form-login Label {
    padding: 10px 0;
}
li{
    list-style: none;
}


.orange{
    background-color: orange;
    display: flex;
    justify-content: center;
}

.blue{
    background-color: paleturquoise;
}

.blue a{
    padding-bottom: 20px;
}

.green{
    background-color: greenyellow;
}

.pink{
    background-color: hotpink;
}

img{
    width: 50px;
}
